<script>
    import { 
        Col, 
        Container, 
        Row,
        Card,
        CardBody,
        CardFooter,
        CardHeader,
        CardSubtitle,
        CardTitle,
        Icon,
        CardText,
        Carousel, 
        CarouselControl, 
        CarouselItem,
        CarouselIndicators,
    } from 'sveltestrap';
    import { push } from 'svelte-spa-router';
    import { onMount } from 'svelte';
    import { fly, fade } from 'svelte/transition';

    export let baseUrl;

    let works = [];
    let caroselIndexes = [];

    onMount(async () => {
        let data = await fetch(`${baseUrl}/works/index.json`).then(data => data.json());
        works = await data.result.pages;
        caroselIndexes = [...new Array(works.length).fill(0)];
    });
</script>



<Container class="mt-3">
    <Row>
        <Col>
            {#if !works.length}
                <div style="display: flex; justify-content: center; margin-bottom: 2rem;" in:fade>
                    No work yet.
                </div>
            {:else}
                {#each works as work, i}
                    <div class="post-card mb-2" in:fly="{{ y: 20, duration: 200 + 100 * i, delay: 400 }}" out:fly="{{ y: -20, duration: 200 }}">
                        <Card>
                            <CardHeader>
                                <CardSubtitle class="card-subtitle text-muted">
                                    Created on
                                    {['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'][parseInt(work.params.date.split('-')[1] - 1)]} 
                                    {parseInt(work.params.date.split('-')[2].split('T')[0])},
                                    {work.params.date.split('-')[0]}
                                </CardSubtitle>
                                <CardTitle class="h5">{@html work.params.title} </CardTitle>
                            </CardHeader>
                            <CardBody>
                                <Row>
                                    <Col xs="12" sm="12" md="6" lg="6">
                                        <div style="height: 100%; display: grid;">
                                            <CardText>
                                                {@html work.content}
                                            </CardText>
                                            <div style="vertical-align: bottom;">
                                                <a class="btn btn-primary btn-sm mr-1 {(!work.params.download_url) ? 'disabled' : ''}" href="{work.params.download_url}" target="_blank">
                                                    <Icon name="download" />
                                                    Download
                                                </a>
                                            
                                                <a class="btn btn-danger btn-sm mr-1 {(!work.params.demo_url) ? 'disabled' : ''}" href="{work.params.demo_url}" target="_blank">
                                                    <Icon name="box-arrow-up-right" />
                                                    Demo
                                                </a>
                                            </div>
                                        </div>
                                    </Col>
                                    <Col xs="12" sm="12" md="6" lg="6">
                                        <Carousel dark items={work.params.images} bind:activeIndex={caroselIndexes[i]}>
                                            <CarouselIndicators bind:activeIndex={caroselIndexes[i]} items={work.params.images} />
                                            <div class="carousel-inner">
                                              {#each work.params.images as image, index}
                                                <CarouselItem bind:activeIndex={caroselIndexes[i]} itemIndex={index}>
                                                  <img src={baseUrl + '/' +image} class="d-block w-100" alt={`${image} ${index + 1}`} />
                                                </CarouselItem>
                                              {/each}
                                            </div>
                                            <CarouselControl direction="prev" bind:activeIndex={caroselIndexes[i]} items={work.params.images} />
                                            <CarouselControl direction="next" bind:activeIndex={caroselIndexes[i]} items={work.params.images} />
                                        </Carousel>
                                    </Col>
                                </Row>
                            </CardBody>
                            <CardFooter>
                                {#each work.params.tags as tag}
                                    <button class="btn btn-success btn-custom mr-1" on:click={() => push('/?&tag=' + tag)}>
                                        <Icon name="tag-fill" />
                                        {tag}
                                    </button>
                                {/each}
                            </CardFooter>
                        </Card>
                    </div>
                {/each}
            {/if}
        </Col>
    </Row>
</Container>

<style>
    .post-card :global(.card .card-header .card-title) {
        margin-bottom: 0px;
    }

    .post-card :global(.card .card-header .card-subtitle) {
        margin-top: -0.2rem;
    }

    .btn-custom {
        padding: 0.35em 0.65em;
        font-size: 0.75em;
        font-weight: 700;
        line-height: 1;
        border-radius: 0.5rem;
    }

    .mr-1 {
        margin-right: 2px;
    } 
</style>